var $ = require('jquery');
var moment = require('moment');
var _core = require('lodash/core');
var _array = require('lodash/array');
var _math = require('lodash/math');
var Chart = require("chart.js");
var Data = require('./terror_data');
var utils = require('./utils');

var MONTHS = ["January", "February", "March", "April", "May", "June", "July", "August", "September", "October", "November", "December"];

window.onload = function() {
    var ctx = document.getElementById("canvas").getContext("2d");
    window.myBar = new Chart(ctx, {
        type: 'bubble',
        data: barChartData,
        options: {
            title: {
                display: true,
                text: 'Islamic terror...'
            },
            responsive: true,
            legend: {
                position: 'top',
            },
            scales: {
                xAxes: [{
                    type: 'time',
                    time: {unit: 'month'},
                    scaleLabel: {
                        display: true,
                        labelString: 'Date'
                    }
                }],
                yAxes: [{
                    type: 'logarithmic',
                    ticks: {
                        min: 2,
                    }
                }]
            },
            tooltips: {
                mode: 'single',
                callbacks: {
                    title: function(tooltipItems, data) {
                        return getRowFromToolTipData(tooltipItems[0],data)[Data.COL.DATE].format("YYYY-MM-DD");

                    },
                    afterTitle: function(tooltipItems, data) {
                        return [
                            getRowFromToolTipData(tooltipItems[0],data)[Data.COL.COUNTY],
                            getRowFromToolTipData(tooltipItems[0],data)[Data.COL.CITY]
                        ];

                    },
                    label : function(tooltip, data){
                        var dataset = data.datasets[tooltip.datasetIndex];
                        value = dataset.label + ": " + dataset.data[tooltip.index].value;
                        return value;
                    },
                    afterBody: function(tooltipItems, data) {
                        var row = getRowFromToolTipData(tooltipItems[0], data);
                        return utils.wordWrapToStringList(row[Data.COL.DESCRIPTION], 60);

                    }
                }
            }
        }
    });
    var getRowFromToolTipData = function(tooltip, data) {
        var dataset = data.datasets[tooltip.datasetIndex];
        var row = dataset.data[tooltip.index].rowData;
        return row;

    };
    // initialize with selected years data.
    loadData($('#select_year').val(), function(){
        // show the most recent month
        lastMonth = $('#select_month option:last').val();
        $('#select_month').val(lastMonth);
        updateChart();

    });

};
var LOADED_DATA;

var barChartData = {
    labels: [],
    datasets: [{
        hidden: false,
        label: 'Killed',
        borderColor: 'rgb(255, 0, 0, 1.0)',
        backgroundColor: "rgba(220,0,0,0.5)",
        data: []
    }, {
        hidden: false,
        label: 'Injured',
        borderColor: 'rgb(0, 0, 255, 0.7)',
        backgroundColor: "rgba(151,187,205,0.5)",
        data: []
    },]

};

var loadData = function(year, callback) {
    $.getJSON("data/terror_" + year + ".json", function (json) {
        LOADED_DATA = json.rows;
        Data.convertDateColToMoment(LOADED_DATA);
        fillMonths(LOADED_DATA);
        fillCounties(LOADED_DATA);
        callback();
    });
};

var getBubbleData = function(rows, col){
    var radius;
    var valueForLog;
    var rowsNonZero = _core.filter(rows, function(row) {
        // as log 0 is infinity make sure we start at 1 (log(1) == 0).
        return row[col] > 0;
    });
    return _core.map(rowsNonZero, function(row) {
        // as log 0 is infinity make sure we start at 1 (log(1) == 0).
        valueForLog = row[col] + 1;
        // Make the circles area proportional to amount
        radius = Math.sqrt(row[col]*2 / Math.PI) * 3;
        radius = Math.max(5,radius);

        return {
            rowData: row,
            value : row[col],
            x : row[Data.COL.DATE],
            y : valueForLog,
            r : radius
        };
    });

};

var updateChart = function (){
    var year = $('#select_year').val();
    var month = $('#select_month').val();
    var country = $('#select_country').val();
    var rows = LOADED_DATA;

    rows = Data.filterMonth(month, rows);
    rows = Data.filterCountry(country, rows);
    var killsData = getBubbleData(rows, Data.COL.KILLS);
    var injuredData = getBubbleData(rows, Data.COL.INJURED);

    var textInfo = " " + year;
    if (month !== 'all') {
        textInfo += " " +  MONTHS[month];
    }
    if (country !== 'all') {
        textInfo += " " + country;
    }
    textInfo += " (Attacks:" + rows.length;
    textInfo += " Killed:" + _math.sumBy(rows, function(row) { return row[Data.COL.KILLS];} );
    textInfo += " Injured:" + _math.sumBy(rows, function(row) { return row[Data.COL.INJURED];} ) + ")";

    barChartData.datasets[0].label = "Killed";
    barChartData.datasets[0].data = killsData;

    barChartData.datasets[1].label = "Injured";
    barChartData.datasets[1].data = injuredData;
    window.myBar.options.title.text = ["Islamic Terror", textInfo];
    window.myBar.update();
    return rows;

};

var fillCounties = function(data){
    var select = $('#select_country') || 'all';
    var selected = select.val();
    var countries = _core.map(data, function(row) {
       return row[Data.COL.COUNTY].toLowerCase();
    });
    if (selected != 'all') {
        // we want to keep the selected country even is it has no enteries for loaded period.
        countries.push(selected);
    }
    countries = _array.uniq(countries);
    countries = _core.sortBy(countries);

    select.empty();
    select.append($('<option/>', {
        value: 'all',
        text : 'All'
    }));
    _core.forEach(countries, function(country) {
        select.append($('<option/>', {
            value: country,
            text : country
        }));
    });
    select.val(selected);
};
var fillMonths = function(data){
    var select = $('#select_month');
    var selected = select.val() || 'all';
    var months = _core.map(data, function(row) {
        return row[Data.COL.DATE].month();
    });

    months = _array.uniq(months).sort();

    select.empty();
    select.append($('<option/>', {
        value: 'all',
        text : 'All'
    }));
    _core.forEach(months, function(month) {
        select.append($('<option/>', {
            value: month,
            text : MONTHS[month]
        }));
    });
    select.val(selected);
    if (select.val() === null){
        select.val('all');
    }
};


$('#select_year').change(function() {
    loadData($('#select_year').val(), updateChart);
});

$('#select_month').change(function() {
    updateChart();
});

$('#select_country').change(function() {
    updateChart();
});

